// FONTS

$primary-font: 'DM Sans', sans-serif;
$title-font: 'Titan One', cursive;

// ----- COLORS ------

$white: #ffffff;
$black: #000000;
$gray: #d9d9d9;

$red: #d41443;
$rose: #ffa5ba;
$rose-gray: #907e82;

// accent

$accent-rose: #ffb8ca;
$accent-yellow: #f0d1a5;
$accent-green: #c2e297;

// background

$bg-white: #ffffff;
$bg-rose-light: #fff5f6;
$bg-hero: #ffb8ca;

// title

$title-black: #000000;
$title-white: #ffffff;

$title-red: #d41443;
$title-rose: #ffa5ba;

// text

$text-white: #ffffff;
$text-black: #000000;

$text-red: #d41443;
$text-rose: #ffa5ba;
$text-rose-gray: #907e82;

// button

$button-bg-white: #ffffff;
$button-bg-white-hover: #ecebeb;

$button-bg-red: #d41342;
$button-bg-red-hover: #c7133e;

// icon

$icon-bg-rose: #fea5bb;
